import React, {Component} from 'react';
import IconSnappy from './images/icon-snappy-process.svg';
import IconAffordable from './images/icon-affordable-prices.svg';
import IconPeople from './images/icon-people-first.svg';

export default class NavSection extends Component {
    render() {
        return (
            <div>
                <section className="container">

                    <h2 className="line-top line-violet">We’re different</h2>
        
                    <div className="flex">  
                        <div className="title">
                            <img src={IconSnappy} alt="icon-snappy-process"/>
                            <h3>Snappy Process</h3>

                            <p>Our application process can be completed in minutes, not hours. Don’t get 
                            stuck filling in tedious forms.
                            </p>   
                        </div>

                        <div className="title">  
                            <img src={IconAffordable} alt="icon-affordable-prices"/>
                            <h3>Affordable Prices</h3>

                            <p>We don’t want you worrying about high monthly costs. Our prices may be low, 
                            but we still offer the best coverage possible.
                            </p>
                        </div>

                        <div className="title">  
                            <img src={IconPeople} alt="icon-people-first"/>
                            <h3>People First</h3>

                            <p>Our plans aren’t full of conditions and clauses to prevent payouts. We make 
                            sure you’re covered when you need it.
                            </p>
                        </div>
                    </div>
                </section>

                <section className="container bg-violet">
                    <div className="flex">
                    <div>
                        <h2>Find out more about how we work</h2>
                    </div>
                        <button className="btn">How we work</button>
                    </div>
                </section>
            </div>
        )
    }    
}

